import React from 'react';
import useMedia from '../../../Hooks/useMedia';
import styles from './Produtos.module.css';
import AOS from 'aos';
import 'aos/dist/aos.css';

const Produtos = (props) => {
  React.useEffect(() => {
    AOS.init();
  }, []);
  const mobile = useMedia('(max-width: 40rem)');

  return (
    <div className={`${mobile ? styles.ProdutosMobile : styles.Produtos}`}>
      <section className={styles.BarnerProdutos}>
        <h1 data-aos="fade-up">{props.produto}</h1>
        <img
          src={props.img}
          alt={props.img}
          data-aos-duration="2000"
          data-aos="fade"
        />
      </section>
      <section className={styles.Card}>
        <section className={`${mobile ? styles.TextoMobile : styles.Texto}`}>
          <section>
            <section>
              <h6 data-aos-duration="2000" data-aos="fade-right">
                Um amor simples, feito café da manhã
              </h6>
            </section>
            <p data-aos="fade-right" data-aos-duration="2000">
              Sem a obrigação de ser perfeito Confortável feito pijama e com a
              humanidade de quem acabou de levantar e que tenha fome
            </p>
          </section>
        </section>
        <section
          className={`${
            mobile ? styles.ProdutosImgMobile : styles.ProdutosImg
          }`}
        >
          <img
            data-aos-duration="2000"
            data-aos="fade-left"
            src={props.fotoProdutos}
            alt={props.fotoProdutos}
          />
        </section>
      </section>
      <section className={styles.Card}>
        <section
          className={`${
            mobile ? styles.ProdutosImgMobile : styles.ProdutosImg
          }`}
        >
          <img
            data-aos-duration="2000"
            data-aos="fade-right"
            src={props.fotoProdutos}
            alt={props.fotoProdutos}
          />
        </section>
        <section className={`${mobile ? styles.TextoMobile : styles.Texto}`}>
          <section>
            <h6 data-aos-duration="2000" data-aos="fade-left">
              Um amor simples, feito café da manhã
            </h6>
          </section>
          <p data-aos="fade-left" data-aos-duration="2000">
            Sem a obrigação de ser perfeito Confortável feito pijama e com a
            humanidade de quem acabou de levantar e que tenha fome
          </p>
        </section>
      </section>
    </div>
  );
};

export default Produtos;
