import React from 'react';
import { Link } from 'react-scroll';
import styles from './Menu.module.css';
const Menu = () => {
  return (
    <ul className={styles.Menu} id="topo">
      <Link to="topo" spy={true} smooth={true} offset={90} duration={1500}>
        <a href="#topo">
          <li id="#topo">Home</li>
        </a>
      </Link>
      <Link to="#meio" spy={true} smooth={true} offset={90} duration={1000}>
        <a href="#meio">
          <li>Serviços</li>
        </a>
      </Link>

      <Link to="footer" spy={true} smooth={true} offset={90} duration={1500}>
        <a href="#footer">
          <li>Contato</li>
        </a>
      </Link>
    </ul>
  );
};

export default Menu;
