import React from 'react';
import Facebook from '../Svgs/Facebook';
import InstagranSvg from '../Svgs/InstagranSvg';
import Whatsapp from '../Svgs/Whatsapp';
import styles from './Header.module.css';
import Menu from './Menu';
const Header = () => {
  return (
    <div className={styles.Header}>
      <Menu />
      <section className={styles.Rede} id="topo">
        <section className={styles.DoceMania}>
          <h1 className="text-focus-in">Doce Mania</h1>
        </section>
        <section className={styles.RedeSvg}>
          <a href="https://www.instagram.com/lannydocemania/">
            <InstagranSvg />
          </a>
          <a href="https://www.facebook.com/lannylopescakes">
            <Facebook />
          </a>
        </section>
      </section>
    </div>
  );
};

export default Header;
