import React from 'react';
import styles from './Main.module.css';
import Produtos from './Produtos/Produtos';
import barner from '../Assets/chocolate-geec8e3400_1920.jpg';
import foto from '../Assets/fofuras.jpg';
import Waves from '../Svgs/Waves';

import WhatsAppContato from '../WhatsAppContato/WhatsAppContato';
const Main = () => {
  return (
    <div className={styles.Main}>
      <Produtos img={barner} produto="Café Da  Manhã" fotoProdutos={foto} />
      <Produtos img={barner} produto="Casamento" fotoProdutos={foto} />
      <Produtos img={barner} produto="Fofuras em Doces" fotoProdutos={foto} />
      <WhatsAppContato />
      <Waves />
    </div>
  );
};

export default Main;
